import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import GameListView from '@/views/GameListView.vue'
import MazeGameView from '@/views/Game/MazeGameView.vue'
import SudokuGameView from '@/views/Game/SudokuGameView.vue'
import MotaGameView from '@/views/Game/MotaGameView.vue'
import VikingGameView from '@/views/Game/VikingGameView.vue'
import LnkjumpGameView from '@/views/Game/LnkjumpGameView.vue'
import NotFoundView from '@/views/NotFoundView.vue'

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/GameList/',
    name: 'GameListView',
    component: GameListView,
  },
  {
    path: '/GameList/MazeGame/',
    name: 'MazeGameView',
    component: MazeGameView,
  },
  {
    path: '/GameList/SudokuGame/',
    name: 'SudokuGameView',
    component: SudokuGameView,
  },
  {
    path: '/GameList/MotaGame/',
    name: 'MotaGameView',
    component: MotaGameView,
  },
  {
    path: '/GameList/VikingGame/',
    name: 'VikingGameView',
    component: VikingGameView,
  },
  {
    path: '/GameList/LnkjumpGame/',
    name: 'LnkjumpGameView',
    component: LnkjumpGameView,
  },
  {
    path: '/404/',
    name: '404',
    component: NotFoundView,
  },
  {
      path: '/:catchAll(.*)',
      redirect: "/404/"
  }


  // {
  //   path: '/about',
  //   name: 'about',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  // }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

export default router
